<template lang="pug">
.container
  el-card
    div.clearfix(slot="header") 
    el-row(v-if='! binding')
      el-col(type='flex')
        span {{errMsg}}
    el-row(v-if='binding')
      el-col(type='flex')
        user-info
    el-row
      el-button(@click='loginout()',type='text') 
</template>

<script>
export default {
  name: 'main',
  data () {
    return {
      errMsg:'!',
      binding:true
    }
  },
  methods:{
    loginout:function(){
      sessionStorage.removeItem('accessToken');
      this.$router.push({path:"/login"});
    }
  },
  created(){
    this.Bus.$on('godisk',uk=>{
      this.$router.push({path:"/disk"});
    });
    this.Bus.$on('isbinding',msg=>{
      this.binding = (msg!=0);
    })
  }
}
</script>

<style scoped>
</style>


